import firebase from 'modules/database'
import * as tf from '@tensorflow/tfjs'
import * as WebCam from 'react-webcam' 
import styled from 'styled-components';
import { useEffect, useState } from 'react';
import { Css, GlobalStyle } from './SharedStyle';
import { animated, useSpring } from 'react-spring';

const trans = (value) => value;

function Button({children}) {
	const [props, set] = useSpring(()=>({ 
		scale: 1, 
		config: { mass: 1, tension: 350, friction: 6 } 
	}));
	const {scale} = props;
	
	return (
		<AnimatedDiv
		onMouseOver={() => set({
			scale:1.4
		})}
		onMouseLeave={() => set({
			scale:1
		})}
		style={{
			transform: scale.interpolate(v=>`scale(${v})`),
			...props
		}}>
			{children}
		</AnimatedDiv>
	);
}

function App() {
  	return (
 		<AppContainer>
			<GlobalStyle/>
			<Div>
				<Button>fwaFrbr</Button>
				<Button>dsdfsf</Button>
				<Button>fsfefsefsfesfesef</Button>
				<Button>eeef</Button>
				<Button>fnfnnfnf</Button>
				<Button>ee</Button>
			</Div>
    	</AppContainer>
	);
}

const AnimatedDiv = styled(animated.div)`
	width: auto;
	height: 20px;
	padding: 10px;
	margin: 5px;
	border-radius: 0px;
	background-color: #ffff80;

	will-change: transform;
	transform-origin: 0 0;
`

const Div = styled.div `
	height: 400px;
	width: 100%;
	background-color: pink;
	${Css.flex_col.vert.in_out_space}
	${Css.flex_col.horz.left}
`

const AppContainer = styled.div`
	background-color: #ffffff;
	width: 100%;
	height: 100%;
`

export default App;
